import React, { Component } from "react";
import styled from "styled-components";
import { debounce } from "lodash";
import girlOne from "../../assets/images/girl-one-min.png";
import girlTwo from "../../assets/images/girl-two-min.png";
import girlThree from "../../assets/images/girl-three-min.png";
import blob from "../../assets/images/blob.svg";
import blob2 from "../../assets/images/blob2.svg";
import IconSVG from "../../components/IconSVG/IconSVG";
import dotted from "../../assets/images/dots.svg";
import dotted2 from "../../assets/images/dots2.svg";
import IconIMG from "../../components/IconIMG/IconIMG";
import casualOne from "../../assets/images/casual-girl-1.jpg";
import casualTwo from "../../assets/images/casual-girl-2.jpg";
import { Title } from "../../components/Title/Title";
import { Article } from "../../components/Article/Article";

const StyledMain = styled.main`
  position: relative;
  background-color: #c1c9d0;
  overflow: hidden;
  z-index: -3;
  display: flex;
  flex-direction: column;
  border-bottom: 10px dotted white;
  @media only screen and (orientation: landscape) and (min-width: 667px) {
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 40px;
  }
`;
const StyledTitle = styled(Title)`
  transform: ${({ isVisible }) =>
    isVisible ? "translatey(0)" : "translatey(35%)"};
  opacity: ${({ isVisible }) => (isVisible ? "1" : "0")};
  transition: transform 0.35s ease-out, opacity 0.6s ease-out;
  will-change: transform, opacity;
`;
const StyledWrapper = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  min-height: 300px;
  padding: 0 5px 0;
  margin-top: 150px;

  @media only screen and (orientation: landscape) and (min-width: 667px) {
    width: 55%;
    order: 2;
  }

  div.blob {
    position: absolute;
    width: 85%;
    top: -20px;
    right: 3px;
    z-index: -3;
  }
  div.blob2 {
    position: absolute;
    width: 80%;
    top: 0;
    right: 2px;
    z-index: -3;
  }
  div.dotted {
    position: absolute;
    width: 35%;
    bottom: 0;
    left: 5%;
    z-index: -3;
  }
  div.dotted2 {
    position: absolute;
    width: 12%;
    top: -15%;
    right: 5%;
    z-index: -3;
  }

  .girl-one {
    position: absolute;
    top: 0;
    left: 15px;
    width: 37%;
    z-index: 3;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }
  .girl-two {
    position: absolute;
    width: 70%;
    z-index: 2;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }
  .girl-three {
    position: absolute;
    top: 50px;
    right: 15px;
    width: 30%;
    z-index: 3;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }
`;

const StyledArticle = styled(Article)`
  margin-top: 10px;
  border-bottom: 10px double white;
  @media only screen and (orientation: landscape) and (min-width: 667px) {
    width: 40%;
    order: 1;
    margin: 140px 0 30px 20px;
  }
`;

const PicsWrapper = styled.div`
  display: flex;
  justify-content: center;
  height: 40vh;
  width: 100%;
  overflow: hidden;

  @media only screen and (orientation: landscape) and (min-width: 568px) {
    order: 3;
    height: 50vh;
    width: 100%;
    align-self: center;
    margin-left: 10px;
  }
  @media only screen and (orientation: landscape) and (min-width: 667px) {
    width: 50%;
    height: 70vh;
  }

  div {
    height: 100%;
    width: 50%;
    position: relative;
    overflow: hidden;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: ${({ isVisible }) =>
        isVisible
          ? "scale(1) translate(-50%, -50%)"
          : "scale(1.5) translate(-50%, -50%)"};
      transform-origin: 0% 0%;
      -webkit-transform: ${({ isVisible }) =>
        isVisible
          ? "scale(1) translate(-50%, -50%)"
          : "scale(1.5) translate(-50%, -50%)"};
      -webkit-transform-origin: 0% 0%;
      transition: transform 0.5s 0.15s ease-out;
      -webkit-transition: transform 0.5s 0.15s ease-out;
      -webkit-transition: -webkit-transform 0.5s 0.15s ease-out;
      height: 100%;
    }
  }
`;
const StyledArticle2 = styled(Article)`
  @media only screen and (orientation: landscape) and (min-width: 667px) {
    width: 40%;
    order: 4;
  }
`;

class Main extends Component {
  state = {
    rendered: false,
    titleIsVisible: true,
    girlOneIsVisible: false,
    girlTwoIsVisible: false,
    girlThreeIsVisible: false,
    picsWrapperisVisible: false
  };

  componentDidMount() {
    window.addEventListener("scroll", this.handleShowPics);
  }

  handleShowPics = debounce(() => {
    const picsWrapper = document.querySelector(".picswrapper");
    console.log("działa");
    let topPicsWrapper = picsWrapper.getBoundingClientRect().top + 10;
    if (topPicsWrapper <= window.innerHeight) {
      this.setState({
        picsWrapperisVisible: true
      });
    } else {
      this.setState({
        picsWrapperisVisible: false
      });
    }
  }, 100);

  render() {
    return (
      <StyledMain>
        <StyledTitle className={"main"} isVisible={this.state.titleIsVisible}>
          prostowanie <br /> keratynowe
        </StyledTitle>
        <StyledWrapper>
          <IconIMG
            src={girlOne}
            className={"girl-one"}
            alt={"dziewczyna z prostymi włosami"}
          />
          <IconIMG
            src={girlTwo}
            className={"girl-two"}
            alt={"dziewczyna z prostymi włosami"}
          />
          <IconIMG
            src={girlThree}
            className={"girl-three"}
            alt={"dziewczyna z prostymi włosami"}
          />
          <IconSVG className={"blob"} src={blob} />
          <IconSVG className={"blob2"} src={blob2} />
          <IconSVG className={"dotted"} src={dotted} />
          <IconSVG className={"dotted2"} src={dotted2} />
        </StyledWrapper>
        <StyledArticle
          className={"articleone"}
          scrollRatio={this.state.scrollRatio}
        >
          <p>
            <strong>Amazon Keratin</strong> to zabieg keratynowy, którego celem
            jest odbudowa struktury włosów, ich intensywne odżywienie i
            wygładzenie. Polega na uzupełnieniu w strukturze włosa niedoboru
            keratyny, będącej naturalnym składnikiem budulcowym włosów.
          </p>
        </StyledArticle>
        <PicsWrapper
          className={"picswrapper"}
          isVisible={this.state.picsWrapperisVisible}
        >
          <div>
            <IconIMG
              className={"casualone"}
              src={casualOne}
              alt={"Dziewczyna po prostowaniu włosów"}
            />
          </div>
          <div>
            <IconIMG
              className={"casualtwo"}
              src={casualTwo}
              alt={"Dziewczyna po prostowaniu włosów"}
            />
          </div>
        </PicsWrapper>
        <StyledArticle2 className={"articletwo"}>
          <p>
            <strong>Preparaty keratynowe</strong>, używane podczas zabiegu,
            zawierają nie tylko keratynę, lecz także{" "}
            <strong>antyoksydanty, wyciągi z roślin i witaminy</strong> , które
            trwale nawilżają włosy i dostarczają niezbędnych składników
            odżywczych oraz kwasy tłuszczowe, które tworzą na włosach
            niewidoczna barierę ochronna przed szkodliwymi czynnikami
            zewnętrznymi.
          </p>
        </StyledArticle2>
      </StyledMain>
    );
  }
}

export default Main;
